.StripeDonationForm {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
}

.StripeDonationForm section {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 190px;
  margin: 0 auto;
  position: relative;
  left: 25px;
  text-align: justify;
  padding-bottom: 15px;
}

@media only screen and (min-width: 400px) {
  .StripeDonationForm section {
    width: 190px;
  }
}

@media only screen and (min-width: 501px) {
  .StripeDonationForm section {
    left: 75px;
  }
}



